idth:470px;margin:0 auto}.cell .bg{font-weight:700;line-height:1;opacity:0;font-size:8em;position:absolute;top:-.2em;right:-.05em;transition:.25s ease-out;letter-spacing:-.07em}.cell:hover .bg,.selected .bg{opacity:0.05;transition:.5s ease-in}.col{-webkit-flex-grow:0;flex-grow:0;-webkit-flex-basis:calc(100%/7);flex-basis:calc(100%/7);width:calc(100%/7)}.col{color:white}.disabled{color:#777}.popUp{height:100vh;width:100vw;background-color:rgba(0,0,0,0.4);position:fixed;top:0;left:0;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.popUp__title{font-size:17px;color:#A2A2A2}.popUp__container{padding:10px 70px 10px 20px;border:1px solid #FDD000}.popUp__close{position:absolute;top:0;right:0;background-color:#FDD000;padding:5px;cursor:pointer}.popUp__content{background-color:white;padding:27px 20px;position:relative}.close{display:none}

